import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';

const blogData = [
  {
    id: 1,
    image: require('../assets/images/blog1.jpg'),
    // time :
    title: 'CGSM',
    description: 'Credit Guarantee Scheme for Micro Small and Medium Enterprise',
    link: '#contact'
  },
  {
    id: 2,
    image: require('../assets/images/blog2.jpg'),
   // time: '10 Nov 2016',
    title: 'MSEP',
    description: 'To support the sustainability and growth of Micro and Small Enterprises (MSEs)',
    link: '#contact'
  },
  {
    id: 3,
    image: require('../assets/images/blog3.jpg'),
     // time: '07 Nov 2016',
    title: 'PMEGP',
    description: 'Prime Minister Employement Guarantee Program',
    link: '#contact'
  }
]

function AppBlog() {
  return (
    <section id="blog" className="block blog-block">
      <Container fluid>
        <div className="title-holder">
          <h2>Latest from blog</h2>
          <div className="subtitle">get our latest news from blog</div>
        </div>
        <Row>
          {
            blogData.map(blog => {
              return (
                <Col sm={4} key={blog.id}>
                  <div className='holder'>
                    <Card>
                      <Card.Img variant="top" src={blog.image} />
                      <Card.Body>
                        <time>{blog.time}</time>
                        <Card.Title>{blog.title}</Card.Title>
                        <Card.Text>
                          {blog.description}
                        </Card.Text>
                        <a href={blog.link} className="btn btn-primary">Coming Soon.. <i className="fas fa-chevron-right"></i></a>
                      </Card.Body>
                    </Card>
                  </div>
                </Col>
              )
            })
          }
        </Row>
      </Container>
    </section>
  )
}

export default AppBlog;